.contacts {
  padding-block: 64px;

  @include onTablet {
    padding-block: 120px;
  }

  &__title {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 56px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 48px;

    @include onTablet {
      gap: 80px;
    }

    @include onDesktop {
      @include pageGrid;
    }
  }

  &__form {
    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include onDesktop {
      grid-column: 8 / -1;
    }
  }

  &__contact-title {
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 8px;
    color: #9393a3;
    font-size: 14px;
  }

  &__contact {
    color: #191919;
    transition: color $effectDuration;

    &:hover {
      color: #F98921; 
    }
  }
}