.recommended {
  padding-block-start: 64px;

  @include onTablet {
    padding-block-start: 120px;
  }

  &__title {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 56px;
    }
  }

  &__products {
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include onTablet {
      gap: 56px;
    }

    @include onDesktop {
      @include pageGrid;
    }
  }

  &__product {
    display: flex;
    flex-direction: column;

    @include onDesktop {
      grid-column: span 4;
    }

    &-photo {
      width: 100%;
      margin-bottom: 24px;
      border-radius: 4px;
      @include hover(transform, scale(1.03));  
    }

    &-title {
      margin-bottom: 8px;
      font-size: 18px;
      font-weight: 700;
      line-height: 140%;
      letter-spacing: normal;
    }

    &-description {
      margin-bottom: 12px;

      @include onDesktop {
        max-width: 280px;
        min-height: 134.4px;
      }

      @media (min-width: 1031px) {
        min-height: max-content;
      }
    }

    &-price {
      font-size: 16px;
      font-weight: 700;
      line-height: 140%;
      letter-spacing: normal;
    }
  }
}