.button {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  height: 56px;
  width: 100%;
  background-color: #F98921;
  border-radius: 8px;
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  border: none;
  line-height: 168.75%;
  transition: all $effectDuration;

  &:hover {
    background-color: #1B2129;
  }

  &:active {
    background-color: #010810;;
  } 
}