.smart-home {
  padding-block-start: 96px;

  @include onTablet {
    padding-block-start: 120px;
  }

  &__img {
    display: block;
    min-height: 200px;
    width: 100%;
    object-fit: cover;
    object-position: center;

    @include onTablet {
      min-height: 400px;
    }

    @include onDesktop {
      min-height: 460px;
    }
  }
}