.products {
  padding-block-start: 96px;

  @include onTablet {
    padding-block-start: 120px;
  }

  &__title {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 56px;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include onTablet {
      gap: 56px;
    }
  }

  &__product {
    &-photos {
      @include pageGrid;
      margin-bottom: 16px;
    }

    &-link {
      overflow: hidden;

      @include onTablet {
        grid-column: span 3
      }

      @include onDesktop {
        grid-column: span 6;
      }
    }

    &-photo {
      width: 100%;
      height: 100%;
      object-fit: cover;

      @include hover(transform, scale(1.1));
    }

    &-title {
      font-size: 18px;
      font-weight: 700;
      line-height: 140%;
      letter-spacing: normal;

      @include onTablet {
        font-size: 24px;
      }
    }
  }

  &__button-wrapper {
    @include onDesktop {
      @include pageGrid;
    }
  }

  &__button {
    grid-column: span 4;
  }
}