.about-us {
  padding-block: 48px;
  background-color: #FCFAEF;

  @include onTablet {
    padding-block: 120px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include onTablet {
      @include pageGrid;
    }
  }

  &__title {
    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__description {
    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 8 / -1;
    }
  }
}