.form {
  display: flex;
  flex-direction: column;
  gap: 16px;

  &__field {
    font-family: "Manrope", sans-serif;
    box-sizing: border-box;
    color: $main-text-color;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    padding: 14px 16px;
    border-radius: 8px;
    outline: none;
    background-color: #F9F9F9;
    border: 1px solid transparent;

    &:-webkit-autofill {
      transition: background-color 999999s;
      -webkit-text-fill-color: $main-text-color;
    }

    &::placeholder {
      font-family: "Manrope", sans-serif;
      font-size: 14px;
      font-weight: 400;
      line-height: 140%;
      color: #7e7e83;
    }

    &:hover {
      border-color: #D5D7DE;
      border-color: #DBDBDB;
    }

    &:hover::placeholder {
      color: $main-text-color;
    }
d
    &:focus {
      border: 2px solid #F98921;
    }
  }

  &__field--textarea {
    min-height: 148px;
    resize: none;
    margin-bottom: 16px;
  }
}