.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: $menu-bg-color;
  font-weight: 700;

  &__nav {
    margin-bottom: 48px;
  }

  &__top-bar {
    margin-bottom: 32px;

    @include onTablet {
      margin-bottom: 56px;
    }
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    text-transform: uppercase;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 5px;
    border-bottom: 1px solid $main-text-color;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    text-transform: uppercase;
  }
}